import React, {Component} from 'react';
import {Alert, Badge, Button, ButtonGroup, Card, Col, Container, Jumbotron, Row} from "react-bootstrap";
import '../media-req.css'
import {RouteComponentProps} from "react-router";
import {tickets_list} from "../StaticData/Tickets";

interface MatchParams {
}

export interface HomePageProps extends RouteComponentProps<MatchParams> {
}

export type HomePageState = {}


export class HomePage extends Component<HomePageProps, HomePageState> {
    constructor(props: HomePageProps) {
        super(props);
        this.state = {};
    }

    componentDidMount(): void {
    }

    render() {
        return (
            <Container style={{height: '100vh'}} fluid='sm'>
                <Row style={{maxHeight: '19%'}}>
                    <Col>
                        <Card>
                            <Card.Body>
                                ##################тестирование на типах задач
                                <Button
                                    disabled
                                    href={process.env.PUBLIC_URL + '/#/'}>
                                    Просмотр варианта
                                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
                <Row style={{height: '80%'}}>
                    <Card style={{height: '100%'}}>
                        <Card.Header as='h4'>
                            <b>Список билетов</b>
                        </Card.Header>
                        <Card.Body style={{height: '100%', overflow: 'auto'}}>
                            <Container fluid>
                                <Row>
                                    {tickets_list.map((ticket) => (
                                        <Card className='card-ticket-home'>
                                            <Card.Header>
                                                <b>Вариант №{ticket.id}</b>
                                            </Card.Header>
                                            <Card.Body>
                                                <Card.Text>
                                                    <Alert variant='dark'>Количество
                                                        вопросов: <b>{ticket.problems.length}</b>
                                                        <h4>
                                                            <Badge
                                                                variant={ticket.is_full_exam_version ? 'success' : 'warning'}>
                                                                {ticket.is_full_exam_version ? 'Полный вариант' : 'Неполный вариант'}
                                                            </Badge>
                                                        </h4>
                                                    </Alert>
                                                </Card.Text>
                                                <Card.Text>
                                                    <ButtonGroup vertical style={{width: '100%'}}>
                                                        <Button
                                                            href={process.env.PUBLIC_URL + '/#/ticket/' + ticket.id}>
                                                            Просмотр варианта
                                                        </Button>

                                                        <Button variant='info'
                                                                href={process.env.PUBLIC_URL + '/#/testing/' + ticket.id}>
                                                            Пройти тестирование
                                                        </Button>
                                                    </ButtonGroup>
                                                </Card.Text>
                                            </Card.Body>
                                        </Card>
                                    ))}

                                </Row>
                            </Container>
                        </Card.Body>
                    </Card>
                </Row>
            </Container>
        );
    }
}